@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

@layer base {
  :root {
    --color-1: 0 100% 63%;
    --color-2: 270 100% 63%;
    --color-3: 210 100% 63%;
    --color-4: 195 100% 63%;
    --color-5: 90 100% 63%;
  }
  
  html {
    font-family: 'Inter', system-ui, sans-serif;
  }
  
  body {
    @apply bg-gray-50 text-gray-900;
  }
}

@layer components {
  .card {
    @apply bg-white rounded-lg shadow-sm border border-gray-200 p-6 transition-all duration-200;
  }
  
  .card:hover {
    @apply shadow-md;
  }
  
  .btn-primary {
    @apply bg-primary-600 hover:bg-primary-700 text-white font-medium py-2 px-4 rounded-md transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2;
  }
  
  .btn-secondary {
    @apply bg-gray-100 hover:bg-gray-200 text-gray-700 font-medium py-2 px-4 rounded-md transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2;
  }
  
  .input-field {
    @apply w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-colors duration-200;
  }
  
  .sidebar-item {
    @apply w-full flex items-center space-x-3 px-4 py-3 rounded-lg text-left transition-all duration-200 group;
  }
  
  .sidebar-item-active {
    @apply bg-primary-50 text-primary-700 border border-primary-200;
  }
  
  .sidebar-item-inactive {
    @apply text-gray-700 hover:bg-gray-50 hover:text-gray-900;
  }
  
  .message-bubble-user {
    @apply max-w-[70%] rounded-lg p-3 bg-primary-600 text-white;
  }
  
  .message-bubble-ai {
    @apply max-w-[70%] rounded-lg p-3 bg-gray-100 text-gray-900;
  }
  
  .todo-item {
    @apply group flex items-center space-x-3 p-4 bg-white border rounded-lg transition-all hover:shadow-sm;
  }
  
  .todo-item-completed {
    @apply border-green-200 bg-green-50;
  }
  
  .todo-item-pending {
    @apply border-gray-200;
  }
  
  .note-item {
    @apply p-3 rounded-lg cursor-pointer transition-colors group;
  }
  
  .note-item-active {
    @apply bg-primary-50 border border-primary-200;
  }
  
  .note-item-inactive {
    @apply hover:bg-gray-50;
  }
  
  .loading-spinner {
    @apply animate-spin rounded-full h-4 w-4 border-b-2 border-primary-600;
  }
  
  .fade-in {
    @apply animate-fade-in;
  }
  
  .slide-up {
    @apply animate-slide-up;
  }
}

/* 自定义滚动条样式 */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* 响应式设计优化 */
@media (max-width: 768px) {
  .sidebar {
    @apply w-16;
  }
  
  .sidebar .sidebar-text {
    @apply hidden;
  }
  
  .main-content {
    @apply p-2;
  }
  
  .card {
    @apply p-4;
  }
}

/* 打印样式 */
@media print {
  .sidebar,
  .header {
    @apply hidden;
  }
  
  .main-content {
    @apply w-full p-0;
  }
}

/* 深色模式支持（可选） */
@media (prefers-color-scheme: dark) {
  .dark-mode {
    @apply bg-gray-900 text-gray-100;
  }
  
  .dark-mode .card {
    @apply bg-gray-800 border-gray-700;
  }
  
  .dark-mode .input-field {
    @apply bg-gray-800 border-gray-600 text-gray-100;
  }
}

/* 无障碍优化 */
.focus-visible {
  @apply outline-none ring-2 ring-primary-500 ring-offset-2;
}

/* 动画优化 */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}